html,
body {
  margin: 0;
  padding: 0;
}

#one-click {
  background: white;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

#main-view {
  flex: 1;
  position: relative;
  display: flex;
}

#slides-container {
  flex: 1;
  padding: 40px;
  padding-bottom: 0px;
  display: flex;
  z-index: 1;
}

#slide-deck {
  flex: 1;
}

.progress-bars {
  padding: 0px 40px;
  z-index: 2;
}

.slides-controls {
  margin-bottom: 10px;
  padding: 0px 40px;
}

.curr-slide-num-label {
  padding: 12px 0px;
  text-align: center;
  align-self: stretch;
  width: 30vw;
  border-radius: 30px;
  background-color: white;
  display: flex;
}

.curr-slide-num-label > p {
  margin: auto;
  font-size: 22px;
  font-weight: bold;
  font-family: "Roboto Mono";
}

.one-click-ctrl-bar {
  background-color: #b8b8b8;
  padding: 15px 30px;
}

.ctrl-move-slide-btn:focus:not(:focus-visible) {
  border: none;
  background-color: white;
}

.ctrl-move-slide-btn-label {
  margin: 0;
  color: black;
  font-size: 16px;
  font-weight: 500;
  font-family: "Roboto Mono";
  width: 100%;
}

.ctrl-btn-icon {
  color: black;
  border-radius: 20px;
}

.tailspin-loader {
  align-items: center;
  justify-content: center;
  min-width: 100%;
  min-height: 100%;
  background-color: rgb(20, 20, 20);
}

#poll-graph-container {
  background: white;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.poll-btns-container {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
}

.poll-btns-title {
  flex: 0 0 100%;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 16px;
}
